.Video-container {
  user-select: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f7f5f2;
  padding: 12px 16px;
  border-radius: 8px;
  box-sizing: border-box;
  overflow: hidden;
  cursor: pointer;
}

.Video-container {
  position: relative;
  max-width: 329px;
  min-height: 410px;
  transition: all 0.2s ease;
}

.Video-container:hover {
  /* transform: translate(0px, -10px); */
  box-shadow: 0px 2px 5px 2px rgb(156, 156, 156);
  transform: scale(1.05);
}

@media only screen and (max-width: 999px) {
  .Video-container {
    width: calc(100% - 10px);
  }
}

@media only screen and (min-width: 999px) and (max-width: 1300px) {
  .Video-container {
    width: calc(50% - 10px);
  }
}

@media only screen and (min-width: 1300px) {
  .Video-container {
    width: calc(32%);
  }
}

.Video-container.checked {
  background-color: #c8fff8;
}

.Video-container:hover {
  background-color: #e2e2e2;
}
.Video-container.checked:hover {
  background-color: #c8fff8;
}

.videoArea {
  height: 200px;
  width: 297px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  border-radius: 10px;
}

.videoArea.skeleton {
  background-color: #edebe7;
}

.Video-details {
  text-align: center;
  width: 100%;
  font-size: 18px;
}

.Video-heading {
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 0em;
  width: 100%;
  padding-bottom: 5px;
  align-items: center;
  font-size: 20px;
  margin-bottom: 8px;
}

.Video-details > div {
  flex: 1;
  display: flex;
  align-items: center;
  border-top: 1px solid #ccc;
  padding: 6px 16px;
  text-align: left;
}

.Video-container.checked .Video-details > div {
  border-color: #979797;
}

.Video-details > div:first-child {
  border: none;
}

.Video-details > div > div {
  width: 100%;
}

.play-button {
  position: absolute;
  top: 30%;
  left: 49%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  cursor: pointer;
  color: #2f0800;
  z-index: 1;
}

.hover-effect:hover {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition:
    transform 0.3s ease,
    box-shadow 0.3s ease;
}

.Video-details .text-element {
  font-family: 'Source Sans Pro';
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
  width: 160.5px;
  height: 24px;
}

.Video-details .text-element {
  font-family: 'Source Sans Pro';
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  width: 100%;
}

.loading__dot {
  animation: dot 4s infinite;
  height: 3px;
  width: 3px;
  margin: 0.7em 1px 0px;
  display: inline-block;
  border-radius: 5px;
}

.loading__dot:nth-of-type(1) {
  animation-delay: 1s;
}

.loading__dot:nth-of-type(2) {
  animation-delay: 2s;
}

.loading__dot:nth-of-type(3) {
  animation-delay: 3s;
}
@keyframes dot {
  0% {
    background-color: #ffffff;
  }
  100% {
    background-color: transparent;
  }
}
