* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 100%;
}
.app {
  display: flex;
}

.dark-background {
  background-color: #2c0d00;
  width: 100% !important;
}

.main-content {
  min-height: 100vh;
  width: calc(100% - 340px) !important;
  position: relative;
}

.spinnerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  z-index: 9999;
  left: 58%;
  top: 40%;
  transform: translate(-50%, -50%);
}

.reRunViewerAlertPosition {
  position: fixed;
  bottom: 20px;
  z-index: 1;
}
.drawerOpen {
  left: 372px;
}
.drawerClosed {
  left: 98px;
}

.d-flex {
  display: flex;
  align-items: center;
}
.d-justify {
  justify-content: center;
}
.d-space-between {
  justify-content: space-between;
}
.df-column {
  flex-direction: column;
}
.df-row {
  flex-direction: row;
}
.df-start {
  justify-content: flex-start;
}
.df-end {
  justify-content: flex-end !important;
}
.df-align-start {
  align-items: flex-start !important;
}
