.device-details {
  width: 100%;
  height: auto;
  gap: 16px;
}

.connected-device .device-details {
  background-color: #f7f5f2;
}
.connected-device .device-details .padding {
  padding: 12px 0px;
}
.connected-device .device-details .padding_icon {
  padding: 10px 0.3vw;
}

.connected-device .device-details .heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Source Sans Pro';
  font-size: 16px;
  font-weight: none;
  letter-spacing: 0px;
  text-align: left;
  width: 100%;
  height: 24px;
  position: relative;
}
.connected-device .device-details .heading svg {
  position: absolute;
  top: 0px;
  right: 0px;
  transition: all 0.4s ease;
}

.connected-device .device-details .heading h5 {
  margin: 0;
}

.connected-device .device-details .heading .image {
  width: 14px;
  cursor: pointer;
}

.connected-device .device-details .icons {
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 10px;
  align-items: center;
}

.connected-device .device-details .icons > span {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0px;
  border-radius: 5px;
}
.connected-device .device-details .icons svg,
.connectionStatus {
  color: #24ac9b;
}
.notSupported {
  color: #ff0000;
}

.connected-device .device-details .icons .rotateIcon {
  transform: rotate(90deg);
}

.connected-device .device-details .icons span {
  font-size: 14px;
  font-family: 'Source Sans Pro';
}

.connected-device .device-details .content p {
  font-family: Source Sans Pro;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
}

.connected-device .device-details .icons > span span {
  padding-left: 10px;
}

.connected-device .device-details .icons img {
  height: 20px;
  width: 20px;
  vertical-align: middle;
}
.middle-sections {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-height: fit-content;
}

.middle-sections > * {
  flex: 0 0 auto;
  min-height: fit-content;
}

/* Middle list styles with responsive behavior */
.middle-list-responsive {
  --middle-gap: 20px;
}

.middle-list-responsive .middle-sections {
  gap: var(--middle-gap);
}

.connected-device {
  flex: 0 0 auto;
  min-height: fit-content;
}

.connected-device .device-details {
  min-height: fit-content;
}

.connected-device .device-details .content {
  min-height: fit-content;
}

/* Responsive styles for smaller screens */
@media (max-height: 800px) {
  .middle-sections {
    gap: 16px;
  }

  .connected-device .device-details .padding {
    padding: 10px 0px;
  }

  .connected-device .device-details .heading {
    font-size: 15px;
    height: 22px;
  }

  .connected-device .device-details .content p {
    font-size: 13px;
    line-height: 20px;
  }
}

@media (max-height: 700px) {
  .middle-sections {
    gap: 12px;
  }

  .connected-device .device-details .padding {
    padding: 8px 0px;
  }

  .connected-device .device-details .heading {
    font-size: 14px;
    height: 20px;
  }

  .connected-device .device-details .content p {
    font-size: 12px;
    line-height: 18px;
  }

  .connected-device .device-details .icons {
    row-gap: 8px;
    column-gap: 8px;
  }
}

@media (max-height: 600px) {
  .middle-sections {
    gap: 8px;
  }

  .connected-device .device-details .padding {
    padding: 6px 0px;
  }

  .connected-device .device-details .heading {
    font-size: 13px;
    height: 18px;
  }

  .connected-device .device-details .content p {
    font-size: 11px;
    line-height: 16px;
  }

  .connected-device .device-details .icons {
    row-gap: 6px;
    column-gap: 6px;
  }

  .connected-device .device-details .icons span {
    font-size: 12px;
  }
}

@media (max-height: 500px) {
  .middle-sections {
    gap: 4px;
  }

  .connected-device .device-details .padding {
    padding: 4px 0px;
  }

  .connected-device .device-details .heading {
    font-size: 12px;
    height: 16px;
  }

  .connected-device .device-details .content p {
    font-size: 10px;
    line-height: 14px;
  }

  .connected-device .device-details .icons {
    row-gap: 4px;
    column-gap: 4px;
  }

  .connected-device .device-details .icons span {
    font-size: 11px;
  }
}

/* Force scrollbar to always be visible in glasses status section */
.glasses-status-section {
  overflow-x: hidden !important;
  overflow-y: scroll !important;
}

.glasses-status-section::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
}

.glasses-status-section::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

/* Also target the container elements */
.glasses-status-container {
  overflow-x: hidden !important;
  overflow-y: scroll !important;
}

.glasses-status-container::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
}

.glasses-status-container::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

/* Prevent horizontal scrollbar during expand/collapse transitions */
.connected-device .device-details .content {
  overflow-x: hidden !important;
}

.device-details {
  overflow-x: hidden !important;
}
