.content-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding: 36px 32px;
}

.left-section,
.folders__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.left-section {
  height: 40px;
}

.folders__container {
  flex-wrap: wrap;
}

.folder_wrapper {
  padding: 20px 0px;
}

.folder_wrapper #headerTable {
  text-align: left !important;
  width: 100%;
}

tr,
th,
td {
  padding: 8px;
  border-bottom: 0.5px solid #ccc;
  border-collapse: collapse;
}
.folderName {
  display: flex;
  align-items: center;
  border: none;
}

button span .css-y6rp3m-MuiButton-startIcon {
  margin-right: 0px !important;
}
