.Video-list {
  font-family: 'Source Sans Pro';
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-items: center;
  height: 100%;
  gap: 23px;
  padding: 0px 32px;
}
@media only screen and (max-width: 999px) {
  .Video-list {
    justify-content: center;
  }
}

.Video-details {
  font-family: 'Source Sans Pro';
  margin-top: 10px;
  text-align: center;
}

.card-checkbox {
  border: 1px solid #333;
  border-radius: 4px;
  width: 15px;
  height: 15px;
  display: inline-block;
  cursor: pointer;
}

.card-checkbox.checked {
  background-color: #333;
}

.banner-msg {
  background-color: #e7fef1;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  column-gap: 25px;
  line-height: 15px;
}

.banner-msg .close-icon {
  cursor: pointer;
}
